main {
	padding-top: 60px;
}
.container {
	max-width: 1920px;
}
.header-ui {
	width: 100%;
	height: 60px;
	background-color: $color-default-white;
	position: fixed;
	z-index: 10;
	&__inner-list {
		display: grid;
		grid-template-columns: 100px repeat(3, 1fr);
		grid-column-gap: 20px;
		list-style: none;
		margin: 0;
		padding: 0;
	}
}

.ui-kit {
	width: 100%;
	height: 100%;
	&__row {
		display: grid;
		grid-template-columns: 100px repeat(3, 1fr);
		grid-column-gap: 20px;
		padding: 50px 0;
		&.btn-row {
			.ui-kit__col {
				grid-row-gap: 30px;
				& > * {
					align-self: center;
				}
			}
		}
		&.socials-row {
			.ui-kit__col {
				grid-template-rows: auto;
			}
		}
		&.card-row {
			.ui-kit__col {
				grid-template-rows: auto;
			}
		}
		&.form-row {
			grid-template-columns: 100px repeat(5, 1fr);
			.ui-kit__col {
				grid-template-rows: auto;
			}
		}
	}
	&__col {
		display: grid;
		grid-template-rows: repeat(3, 1fr);
		grid-row-gap: 1.2rem;
		&-span {
			display: inline-block;
			color: $color-default-black;
			font-weight: 400;
			font-size: 16px;
			line-height: 19px;
		}
	}
	&__socials {
		display: flex;
		flex-wrap: wrap;
		max-width: 220px;
		column-gap: 30px;
		row-gap: 30px;
		&-icon {
			&--insta {
				color: $color-default-white;
			}
		}
	}
	&__card {
		&--type-01 {
			max-width: 270px;
		}
		&--type-02 {
			max-width: 500px;
		}
	}
}

.state {
	&--hover {
		.link {
			color: $color-jade;
			&--color {
				text-decoration: underline;
			}
			&--bolder {
				color: $color-jade;
			}
		}
		.btn {
			&--fill {
				box-shadow: 0px 1px 10px 3px rgba(0, 0, 0, 0.25);
			}
			&--stroke {
				color: $color-default-white;
				background-color: $color-jade;
			}
			&--nav {
				background-color: $color-jade;
				svg {
					color: $color-default-white;
				}
			}
		}
		.ui-kit {
			&__socials {
				&-link {
					&--insta {
						background: $mustard-to-fuchsia;
					}
				}
				&-icon {
					&--fb {
						color: $color-mariner;
					}
					&--tg {
						color: $color-pacific-blue;
					}
					&--tweet {
						color: $color-malibu;
					}
					&--viber {
						color: $color-vivid-violet;
					}
					&--vk {
						color: $color-san-marino;
					}
					&--whatsapp {
						color: $color-fruit-salad;
					}
					&--yt {
						color: $color-cinnabar;
					}
				}
			}
			&__card {
				&--type-01 {
					.goods-card {
						&__wrapper {
							box-shadow: 0px 0px 25px rgba($color-default-black, 0.2);
						}
						&__info {
							&-title {
								color: $color-jade;
							}
						}
						&__btn {
							border-color: $color-jade;
							color: $color-jade;
						}
					}
				}
				&--type-02 {
					.goods-card {
						&__info {
							&-title {
								color: $color-jade;
							}
						}
					}
				}
				&--type-03 {
					.goods-card {
						&__pic {
							filter: drop-shadow(0px 0px 25px rgba($color-default-black, 0.2));
						}
						&__info {
							&-title {
								color: $color-jade;
							}
						}
					}
				}
				&--type-04 {
					.goods-card {
						&__pic {
							box-shadow: 0px 0px 25px rgba($color-default-black, 0.2);
						}
						&__info {
							&-title {
								color: $color-jade;
							}
						}
					}
				}
				&--type-05 {
					.article-card {
						&__wrapper {
							box-shadow: 0px 0px 25px rgba($color-default-black, 0.2);
						}
					}
				}
				&--type-06 {
					.goods-card {
						&__info {
							&-title {
								color: $color-jade;
							}
						}
					}
				}
				&--type-07 {
					.goods-card {
						&__info {
							&-title {
								color: $color-jade;
							}
						}
						&__pic {
							box-shadow: 0px 0px 25px rgba($color-default-black, 0.2);
						}
						&__img {
							filter: brightness(0.7);
						}
						&__link {
							opacity: 1;
							visibility: visible;
							pointer-events: all;
						}
					}
				}
				&--type-08 {
					.article-card {
						&__wrapper {
							filter: drop-shadow(0px 0px 20px rgba($color-default-black, 0.25));
						}
					}
				}
			}
		}
	}
	&--focus {
		.link {
			color: $color-jade;
			@include custom_outline;
			&--color {
				text-decoration: underline;
			}
			&--bolder {
				color: $color-jade;
				outline: none;
				&::after {
					transform: scale(1);
				}
			}
		}
		.btn {
			&--fill {
				@include custom_outline;
			}
			&--stroke {
				@include custom_outline;
			}
			&--nav {
				@include custom_outline;
			}
		}
