						&__btn {
							border-color: $color-jade;
							color: $color-jade;
						}
					}
				}
				&--type-02 {
					.goods-card {
						&__info {
							&-title {
								color: $color-jade;
							}
						}
					}
				}
				&--type-03 {
					.goods-card {
						&__pic {
							filter: drop-shadow(0px 0px 25px rgba($color-default-black, 0.2));
						}
						&__info {
							&-title {
								color: $color-jade;
							}
						}
					}
				}
				&--type-04 {
					.goods-card {
						&__pic {
							box-shadow: 0px 0px 25px rgba($color-default-black, 0.2);
						}
						&__info {
							&-title {
								color: $color-jade;
							}
						}
					}
				}
				&--type-05 {
					.article-card {
						&__wrapper {
							box-shadow: 0px 0px 25px rgba($color-default-black, 0.2);
						}
					}
				}
				&--type-06 {
					.goods-card {
						&__info {
							&-title {
								color: $color-jade;
							}
						}
					}
				}
				&--type-07 {
					.goods-card {
						&__info {
							&-title {
