							}
						}
						&__pic {
							box-shadow: 0px 0px 25px rgba($color-default-black, 0.2);
						}
						&__img {
							filter: brightness(0.7);
						}
						&__link {
							opacity: 1;
							visibility: visible;
							pointer-events: all;
						}
					}
				}
				&--type-08 {
					.article-card {
						&__wrapper {
							filter: drop-shadow(0px 0px 20px rgba($color-default-black, 0.25));
						}
					}
				}
			}
		}
		.form {
			&__input {
				label {
					input {
						border-color: $color-matterhorn!important;
					}
				}
			}
		}
	}
	&--text {
		.form {
			.custom-input {
				label {
					input {
						border-color: $color-matterhorn!important;
					}
				}
				&__label {
					transform: translateY(-50px)!important;
					font-size: 14px!important;
					color: $color-dim-gray!important;
				}
			}
		}
	}
	&--error {
		.form {
			.custom-input {
				pointer-events: none;
			}
		}
	}
}
