// focus
// Если нет дизайн-макета для фокус-состояния, то меняем аутлайн на кастомный, иначе пишем стили из макета

@mixin custom_outline {
  outline: 2px solid $color-outline-custom;
  outline-offset: 2px;
}

@mixin focus($custom_default: false) {
  &:focus-visible {
    @include custom_outline();
    @if $custom_default {
    } @else {
      @content;
    }
  }
}

